/**
 * This is the root component that contains sections for
 * toggling between viewing modes and viewing managed content
 *
 * @flow
 */

"use strict";

var Editor = require("./editor");
var EditorStore = require("stores/editor_store");
var Fullscreen = require("./fullscreen");
var React = require("react");
var Stateful = require("diode/stateful");
var fullscreen = require("fullscreen");

var App = React.createClass({
  displayName: "App",

  mixins: [Stateful],

  propTypes: {
    editorId: React.PropTypes.string.isRequired
  },

  getState: function getState() {
    return {
      editor: EditorStore.find(this.props.editorId)
    };
  },

  goFullscreen: function goFullscreen() {
    fullscreen(this.getDOMNode());
  },

  render: function render() {
    var editor = this.state.editor;

    return React.createElement(
      "div",
      { className: "colonel" },
      React.createElement(Editor, { editor: editor, block: editor.block }),
      React.createElement(Fullscreen, { ref: "fullscreen", onClick: this.goFullscreen })
    );
  }

});

module.exports = App;


